
.yuding {
    width: 100%;
    .ydhd {
        width: 100%;
        height: 1.2rem;
        padding-left: 5%;
        box-sizing: border-box;
        border-bottom: 2px solid #eee;
        i {
            font-size: 0.4rem;
            line-height: 1.2rem;
        }
    }
    .ydbd {
        padding: 0.5rem 5%;
        .yd_tit {
            padding-bottom: 0.3rem;
            border-bottom: 1px solid #eee;
            display: flex;
            justify-content: space-between;
            align-items: center;
            div {
                width: 50%;
                h2 {
                    width: 100%;
                    font-size: 0.3rem;
                    font-weight: bold;
                    line-height: 0.5rem;
                    text-overflow: ellipsis;
                    overflow: hidden;
                    white-space: nowrap;
                }
            }
            img {
                width: 2.5rem;
                height: 1.8rem;
            }
        }
        .yd_date {
            padding: 0.5rem 0;
            border-bottom: 2px solid #eee;
            display: flex;
            justify-content: space-around;
            div {
                text-align: center;
                h5 {
                    font-size: 0.26rem;
                    line-height: 0.5rem;
                    color: #999;
                }
                h2 {
                    font-size: 0.32rem;
                    font-weight: bold;
                    color: #008489;
                }
            }
        }
        .yd_price {
            padding: 0.5rem 0;
            border-bottom: 2px solid #eee;
            h5 {
                font-size: 0.3rem;
                font-weight: bold;
                margin-bottom: 0.3rem;
            }
            div {
                display: flex;
                justify-content: space-between;
                h2 {
                    font-size: 0.32rem;
                    color: #666;
                }
            }
        }
        .yd_msg {
            padding: 0.5rem 0;
            box-sizing: border-box;
            h5 {
                font-size: 0.3rem;
                font-weight: bold;
                margin-bottom: 0.3rem;
            }
            p {
                font-size: 0.27rem;
                line-height: 0.4rem;
                color: #666;
                margin-bottom: 0.1rem;
            }
        }
    }
    .ydft {
        width: 100%;
        height: 1.8rem;
        padding: 0 5%;
        box-sizing: border-box;
        border-top: 1px solid #eee;
        position: fixed;
        bottom: 0;
        display: flex;
        justify-content: space-between;
        align-items: center;
        h5 {
            font-size: 0.32rem;
            font-weight: bold;
            color: #008489;
            span {
                font-size: 0.28rem;
                font-weight: normal;
                margin-left: 0.2rem;
                color: #666;
            }
        }
        button {
            width: 3rem;
            height: 1rem;
            font-size: 0.3rem;
            color: #fff;
            border: none;
            border-radius: 5px;
            background: #ff5a5f;
        }
    }
}